        this.openArr = this.openArr.filter((v) => v !== id);
      } else {
        this.openArr.push(id);
      }
    },
    allClose(section) {
      switch (section) {
        case "sec1":
          this.openArr = this.openArr.filter(
            (v) =>
              v !== "항공" &&
              v !== "항공2" &&
              v !== "숙박" &&
              v !== "라피트" &&
              v !== "ESIM"
          );
          break;
        case "sec2":
          this.openArr = this.openArr.filter(
            (v) =>
              v !== "16일정" &&
              v !== "17일정" &&
              v !== "18일정" &&
              v !== "19일정"
          );
          break;
      }
    },
  },
  async mounted() {},
